import { Link, Card, CardContent, CardMedia, Grid, makeStyles, Typography } from '@material-ui/core'
import mockData from "../mockData"
import React from 'react'


const Myworks = ({title, dark, id}) => {
    const classes = useStyles();
    return (
        
        <div className ={`${classes.section} ${dark && classes.sectiondark}`}>
        <div className={classes.sectioncontent} id={ id}>

                <Grid container className={classes.grid}>
                    {
                        mockData.map(({title, image, link}, index)=>(
                            <Grid item key ={index} xs={12} sm={6} md={6}>
                                <Card className={classes.card}>`
                                     <CardMedia className={classes.thumbnail} image={image} titulo="thumbnail"/>
                                    <div className={classes.overlay}> 
                                        <div className={classes.transparent}></div>
                                   <CardContent className={classes.cardtext}>
                                        <Link  className={classes.title} href={link}  target="_blank" rel="noopener noreferrer">
                                           {title}
                                            </Link>
                                   </CardContent>
                                    </div>`
                                 
                                </Card>
                            </Grid>
                        ))
                    }
                </Grid>
            </div>
    </div>
    )
}

const useStyles = makeStyles((theme) => ({
    section: {
        minHeight: "92vh"
    },
    titlesection:{
        fontFamily: "'Playfair Display', serif;",
        paddingTop: "2rem",
         [theme.breakpoints.down('sm')]:{
            padding: "20px 0px",
            fontSize:"2rem"
        }
        
    },
    titlecategory:{
        marginTop: theme.spacing(4)
    },
    sectiondark: {
        background:  "#000",
        color: "#fff",
    },
    sectioncontent:{
        maxWidth: "90vw",
        margin: "0 auto",
        padding: theme.spacing(5),
        [theme.breakpoints.down('sm')]:{
            padding: theme.spacing(2),

           
        }
    },
    sectiondark: {
        background:  "#000",
        color: "#fff",
    },
    sectioncontent:{
        maxWidth: "100vw",
        margin: "0 auto"
    },
    grid:{
        marginTop: "0px",
      
    },
    card: {
        maxWidth:'50vw', 
        minHeight: '200px',
        height:'470px',
        borderRadius:"0px",
        position:'relative',
        '&:hover. thumbnail':{
         filter: 'blur(3px)',
        },
          [theme.breakpoints.down('sm')]:{
           maxWidth:'100vw',

           
        }
    },
    thumbnail:{
        width:'100%',
        height:'100%',
        marginTop:'-22px',
     
    },
    cardtext:{
        backgroundColor:'#f7337899',
        height:'499px',
        width:'100%',
        display:'flex',
        justifyContent:'center',
        alignItems:'center',
        
    },
    overlay:{
        display:'block',
        position:'absolute',
        bottom:'0',
        height:'100%',
        width:'100%',
        transition:'0.5s',
        '&:hover':{
            transform:'translateY(-500px)',
        },
       

    },
    transparent:{
        height:'490px',
        top:'0',
        backgroundColor:'transparent',
        opacity:'0.5'
    },
    title:{
        color:'white',
        fontSize:'3em',
        zIndex:'1',
        fontWeight:'200',
        padding:'250px 100px',
        '&:hover':{
            textDecoration:'none',
            
        }
    }

}))

export default Myworks


